.relatedVids {
  p {
    margin: 5px 0 20px 0;
    font-size: .8rem;
  }
  .container {
    display: grid;
    row-gap: 25px;
  }
  .video {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    div:nth-child(1) {
      width: 59%;
    }
  }
  .videoInfoContainer {
    width: 37%;
  }
  .relatedVidsErr {
    text-align: center;
    p {
      margin: 0;
      font-size: .8rem;
      color: #282726;
    }
  }
  @media (min-width: 768px) {
    .container {
      grid-template-columns: 1fr 1fr 1fr;
      column-gap: 25px;
    }
    .video {
      display: unset;
      div:nth-child(1) {
        width: unset;
      }
    }
    .videoInfoContainer {
      width: unset;
    }
  }
  @media (min-width: 1024px) {
    padding: 15px 0 0 0!important;
    border-bottom: unset;
    .container {
      grid-template-columns: 1fr;
      column-gap: unset;
      overflow-y: scroll;
      padding-right: 15px;
      height: calc(100vh - 60px - 205px);
    }
    .video {
      display: flex;
      justify-content: unset;
      &:nth-child(1) {
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, .3);
      }
      div:nth-child(1) {
        width: 175px;
        height: 100px;
      }
    }
    .videoInfoContainer {
      margin-left: 15px;
    }
  }
}