.videoInput {
  div {
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    align-items: center;
    padding: 5px 10px;
    width: fit-content;
    overflow: hidden;
    border-radius: 5px;
    margin-bottom: 3PX;
  }
  h3 {
    font-size: .8rem;
    margin: 0 0 10px 0;
    font-weight: normal;

  }
  input {
    color: #F2F2F2;
    background-color: transparent;
    outline: none;
    border: none;
  }
  .invalidBtn {
    background-color: #616467;
  }
  .validBtn {
    background-color: darken(#ff0000, 15);
  }
  .button {
    border: none;
    outline: none;
    padding: 7px 10px;
    width: max-content;
    border-radius: 4px;
    font-size: .8rem;
    font-weight: bold;
  }
  @media (min-width: 1024px) {
    padding-top: unset !important;
    div {
      width: 100%;
      justify-content: space-between;
    }
  }
}