.screen {
  max-width: 1500px;
  margin: 0 auto;
  .content {
    padding: 15px;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
  }
  .pageContent {}
  .videoSection {
    height: 100%;
  }
  .sideContent {
    height: 100%;
  }
  @media (min-width: 1024px) {
    .content {
      padding-left: unset;
    }
    .pageContent {
      padding: 30px 20px;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      height: calc(100vh - 116px);
    }
    .videoSection {
      width: calc(100% * 2/3 - 20px);
    }
    .sideContent {
      width: calc(100% * 1/3 - 20px);;
    }
  }
}




