.screen {
  .pageTitle {
    font-size: 1.1rem;
    margin: 0 0 20px 0;
  }
  .content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 96px);
  }
  .videoMainList {
    padding: 20px 0;
    display: grid;
    row-gap: 25px;
    .videoInfoContainer {
      margin-top: 10px;
    }
    @media (min-width: 768px) {
      grid-template-columns: 1fr 1fr;
      column-gap: 30px;
      .video {
        width: 100%;
      }
    }
    @media (min-width: 1024px) {
      grid-template-columns: 1fr 1fr 1fr;
    }
    @media (min-width: 1200px) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
}