.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #282726;
  .searchBar {
    border-radius: 5px;
    overflow: hidden;
    background-color: rgba(0, 0, 0, .4);
    display: flex;
    align-items: center;
    padding: 5px 10px;
    input {
      color: #F2F2F2;
      background-color: transparent;
      outline: none;
      border: none;
    }
    a {
      outline: none;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        fill: #616467;
      }
    }
  }
  @media (min-width: 768px) {
    .logo {
      display: flex;
      align-items: center;
      img {
        margin-right: 5px;
      }
    }
    .ytname {
      font-size: 1rem;
      font-weight: bold;
      color: #F2F2F2;
    }
    .searchBar {
      width: 50%;
      max-width: 350px;
      justify-content: space-between;
      input {
        width: 100%;
      }
    }
  }
}