.screen {
  width: 100%;
  height: calc(100vh - 56px);
  display: flex;
  align-items: center;
  justify-content: center;
  .error, .notFound {
    width: 65%;
    max-width: 300px;
  }
  .orDivider {
    color: darken(#616467, 10);
    text-align: center;
    font-weight: bold;
    margin: 20px 0 50px 0;
  }
  .error h1 {
    margin: 0 0 20px 0;
  }
  .searchInput {
    input {
      width: 80%;
    }
  }
  h1 {
    font-size: 1.1rem;
  }
  .notFound, .content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .content {
      margin-bottom: 50px;
    }
    h1 {
      margin: 10px 0;
    }
    img {
      width: 60%;
      max-width: 200px;
    }
    span {
      color: #616467;
      font-size: .8rem;
    }
    a {
      margin-top: 20px;
    }
  }
  button, a {
    background-color: darken(#ff0000, 15);
    border: none;
    outline: none;
    padding: 7px 10px;
    border-radius: 4px;
    font-size: .8rem;
    font-weight: bold;
  }
}