.screen {
  .pageTitleContainer {
    margin: 0 0 20px 0;
    font-size: .9rem;
  }
  .pageTitle {
    display: flex;
    align-items: center;
    span:nth-child(1) {
      font-weight: bold;
      color: #616467;
    }
    span:nth-child(2) {
      color: darken(#616467, 10);
    }
  }
  .videoMainList {
    padding: 20px 0;
    display: grid;
    row-gap: 25px;
    .video {
      display: flex;
      align-items: flex-start;
    }
    .videoInfoContainer {
      margin-left: 10px;
    }
  }
  .noResults {
    width: 100%;
    height: calc(100vh - 96px);
    display: flex;
    align-items: center;
    justify-content: center;
    h1 {
      font-size: 1.1rem;
    }
  }
  @media (min-width: 768px) {
    .video div:nth-child(1) {
      width: 30%;
      max-width: 225px;
      height: 130px;
    }
    .content {
      max-width: 850px;
      margin: 0 auto;
    }
  }
}