.video {
  .thumbnail, .video-creator-img {
    overflow: hidden;
  }
  .thumbnail, .videoLength {
    border-radius: 3px;
  }
  .thumbnail {
    position: relative;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .videoLength {
    background-color:  rgba(0, 0, 0, .4);
    color: #F2F2F2;
    font-size: .65rem;
    padding: 3px 7px;
    position: absolute;
    right: 6px;
    bottom: 6px;
  }
  .vidTitle {
    margin: 0;
    color: #F2F2F2;
    font-size: .9rem;
  }
  .infoText {
    font-size: .7rem;
    font-weight: bold;
  }
  .infoText, .dotSeparator {
    color: #616467;
  }
  .dotSeparator {
    font-weight: bold;
    margin: 0 3px;
  }
  .description {
    margin-top: 10px;
    color: lighten(#616467, 20);
    word-wrap: break-word;
    font-size: .7rem;
  }
}