.container {
  h3 {
    margin: 0;
    font-weight: normal;
  }
  .videoTitleContainer {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    h3 {
      font-size: .9rem;
    }
    button {
      background-color: transparent;
      outline: none;
      border: none;
    }
  }
  .creatorContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .creator {
    font-size: .8rem;
    color: hsla(0,0%,93.3%,.6);
    span {
      font-weight: bold;
    }
  }
  .subscribe {
    display: flex;
    align-items: center;
    font-size: .8rem;
    color: darken(#ff0000, 15);
    font-weight: bold;
    svg {
      margin-right: 5px;
    }
  }
  .description {
    line-height: 15px;
    p {
      font-size: .8rem;
      color: hsla(0,0%,93.3%,.6);
      margin: 0;
      white-space: pre-line;
      word-wrap: break-word;
    }
  }
  .viewOnYoutube {
    margin-top: 10px;
    font-size: .7rem;
    font-weight: normal;
    color: darken(darkgray, 20);
    a {
      display: flex;
      align-items: center;
    }
    svg {
      margin-left: 5px;
    }
  }
  @media (min-width: 1024px) {
    .creatorContainer {
      border-bottom: unset;
      div {
        background-color: #616467;
        color: #1A1A1A;
        font-weight: bold;
      }
    }
    .channel {
      padding: 10px;
      border-radius: 3px;
      font-size: .8rem;
    }
    .subscribe {
      svg {
        display: none;
      }
      span {
        color: #F2F2F2;
        background-color: darken(#ff0000, 15);
      }
    }
  }
}